body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 100vh;
  overflow: hidden;
}

.App > nav {
  width: 20em;
  padding: 0 1em;
}

.App > main {
  height: 100%;
  overflow: auto;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.App > main pre {
  max-width: 50em;
}

.App > main p {
  max-width: 40em;
}

.App > main pre {
  white-space: pre-wrap;  
}

.App a {
  color: #1a1a1a;
  text-decoration: none;
}

.App a:hover {
  text-decoration: underline;
}

.App a:hover h5::after {
  content: "\B6";
}

.App > main .module > a h5 {
  font-size: 1.25em;
}

.App pre {
  background: #eee;
  padding: 1em;
  box-sizing: border-box;
}
